import { LOGIN_CHECK, LOGIN_SUCCESS } from "../types";

const INITIAL_STATE = {
    token: null,
    loggedIn: false,
};

const authReducer = (state = INITIAL_STATE, { type, payload }) => {
    switch (type) {
        case LOGIN_SUCCESS:
            return {
                ...state,
                token: payload,
            };
        case LOGIN_CHECK:
            return {
                ...state,
                loggedIn: payload,
            };
        default:
            return state;
    }
};

export default authReducer;
