import React, { useState } from "react";
import { Grid } from "@material-ui/core";
import styled from 'styled-components'
import Start from "views/quiz/Start";
import { modules } from "datas/modules";
import { LOGIN_CHECK } from "store/types";
import { useDispatch } from "react-redux";

const Title = styled.div`
    height: 20vh;
    text-align: center;

    @media screen and (max-width: 800px) {
        height: 10vh;
    }
`;

const Icon = styled.div`
    float: right;
    font-size: 20px;
    cursor: pointer;

    &:hover {
        color: white;
    }
`;

const Box = styled.div`
    width: 150px;
    height: 150px;
    padding: 30px;
    border: 1px solid #616A94;
    background-color: #161A31;
    text-align: center;
    border-radius: 20px;
    cursor: pointer;

    &:hover {
        color: white;
        background-color: #616A94;
    }
`;

function Modules() {
    const dispatch = useDispatch()
    const [goTo, setGoTo] = useState(false);
    const [name,setName] = useState()

    return (
        <>
            <div>
                {goTo ?
                    <Start setGoTo={setGoTo} name={name} /> :
                    <>
                        <Icon>
                        <i className="fa fa-share" 
                            onClick={() => dispatch({ type: LOGIN_CHECK, payload: false })} />
                        </Icon>
                        <Grid container justify="center" spacing={3}>
                            <Grid item lg={12} xs={10}>
                                <Title>
                                    <h2>Select modules and start the quiz</h2>
                                </Title>
                            </Grid>

                            {modules.map((e, i) => {
                                return (
                                    <Grid item key={i}>
                                        <Box onClick={() => {
                                            setName(e.name)
                                            setGoTo(true)
                                        }}>
                                            <i className={e.img} style={{ fontSize: "50px" }} />
                                            <h4> {e.name} </h4>
                                        </Box>
                                    </Grid>
                                )
                            })}
                        </Grid>
                    </>}
            </div>
        </>
    );
}

export default Modules;
