import React, { useState } from 'react'
import { Box, Grid, Modal } from '@material-ui/core'
import styled, { css } from 'styled-components/macro'
import Button from 'views/quiz/Button'
import Login from 'views/auth/Login';
import { useSelector } from 'react-redux';
import Modules from 'views/modules';

const Intro = styled.div`
  margin-top: 5em;
  text-align: justify;

  @media screen and (max-width: 800px) {
    margin-top: 1em;
    font-size: 10px;
}
`;

const Next = styled.div`
  margin-top: 8em;
  text-align: center;

  @media screen and (max-width: 500px) {
    font-size: 10px;
`;

const btnCSS = css`
    margin-top: 2em;
`;

const Loginbox = styled.div`
    position : absolute;
    top : 50%;
    left : 50%;
    transform: translate(-50%, -50%);
    width: 430px;

    @media screen and (max-width: 500px) {
        width : 350px;
`;

const Home = () => {
    const [begin, setBegin] = useState(false);

    const { loggedIn } = useSelector(({ auth: { loggedIn } }) => ({
        loggedIn: loggedIn,
    }))

    return (
        <>
        {loggedIn?
        <Modules/>:
            <Grid container justify='center' spacing={3}>
                <Grid item lg={4} xs={11}>
                    <Intro>
                        <h2>About</h2>
                        <h4>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum.</h4>
                    </Intro>
                </Grid>
                <Grid item lg={6}>
                    <Next>
                        <h1>Learn and test yourself</h1>
                        <h4>Click start to learn now.</h4>
                        <Button style={{ color: "white", backgroundColor: "#616A94" }} onClick={() => setBegin(true)} css={btnCSS}>
                            <i className='fa fa-play' /> Start
                        </Button>
                        &nbsp;&nbsp;&nbsp;
                        <Button css={btnCSS}>
                            Learn more
                        </Button>
                    </Next>
                </Grid>
            </Grid>
        }
        <Modal 
          open={begin}
          onClose={() => setBegin(false)}>
              <Loginbox>
                    <Login setBegin={ setBegin }/>
              </Loginbox>
        </Modal>
        </>
    )
}

export default Home
