import { toast } from "react-toastify";
import { history } from "utils";
import { LOGIN_CHECK, LOGIN_SUCCESS, LOGOUT, TOGGLE_LOADING, TOGGLE_SIDEBAR} from "../types";

export const loginUser = (requestData) => {
    return async (dispatch) => {
        dispatch({ type: TOGGLE_LOADING, payload: true });
        dispatch({ type: TOGGLE_SIDEBAR, payload: false });

        if(requestData) {
            

            dispatch({ type: LOGIN_SUCCESS, payload: "response.token" });
            dispatch({ type: LOGIN_CHECK, payload: true });
            dispatch({ type: TOGGLE_LOADING, payload: false });
            // history.push("/app/home")
        } else {
            toast.error("something went wrong");
            dispatch({ type: TOGGLE_LOADING, payload: false });
        }
    };
};


export const logoutUser = () => {
    return (dispatch) => {
        dispatch({ type: LOGOUT });
    };
};
