import React from "react";
import "./admin.css";
import styled from "styled-components"
import { useLocation, Route, Switch, Redirect } from "react-router-dom";
import { useSelector } from "react-redux";
import { Backdrop, CircularProgress } from "@material-ui/core";
import { adminRoutes } from "routes.js";
// import { isLoggedIn } from "utils";

const Outer = styled.div`
    width: 100%; 
    height: 100vh; 
    padding: 50px; 
    background-color: #7b04e2;
`;

const Inner = styled.div`
    padding: 20px; 
    height: 100%; 
    width: 100%;
    background-color: #101427; 
    border-radius: 20px; 
    overflow-x: scroll;
    box-shadow: 0 3px 50px #565656;
`;

const Admin = () => {
    const location = useLocation();

    const { isAuthenticated, loading } = useSelector(({ auth: { token }, loading }) => ({
        // isAuthenticated: isLoggedIn(token),
        loading,
    }));

    React.useEffect(() => {
        document.documentElement.scrollTop = 0;
        document.scrollingElement.scrollTop = 0;
        // mainContent.current.scrollTop = 0;
    }, [location]);

    const getRoutes = (routes) => {
        return routes.map((prop, key) => {
            if (prop.layout === "/app" ) {
                return <Route path={prop.layout + prop.path} component={prop.component} key={key} />;
            } else {
                return <Redirect to={"/auth/login"} key={key} />;
            }
        });
    };

    return (
        <>
            <Backdrop open={loading} style={{ zIndex: 999 }}>
                <CircularProgress color="primary" />
            </Backdrop>
        
            <Outer>
                <Inner>
                    <Switch>
                        {getRoutes(adminRoutes)}
                        <Redirect from="*" to="/app/home" />
                    </Switch>
                </Inner>
            </Outer>
        </>
    );
};

export default Admin;
